.card {
  position: relative;
  width: 29rem;
  display: flex;
  padding: 2rem 1.5rem 2rem 1.5rem;
  border: 0.1rem solid var(--gray_3);
  border-radius: 0.5rem;
  will-change: border;
  transition: border 0.3s ease-in-out;

  @media (min-width: 375px) {
    width: 27.5rem;
    padding: 2rem 1.5rem 2rem 2rem;
  }

  @media (min-width: 1024px) {
    width: 26rem;
    display: block;
  }

  // @media (min-width: 1920px) {
  //   width: 30rem;
  // }

  &:hover,
  &:focus {
    border: 0.1rem solid var(--red_1);
  }

  span {
    width: 30%;
    @include InterMedium();
    font-size: 2.6rem;
    line-height: 3.1rem;
    color: var(--black_1);
  }

  img {
    position: absolute;
    right: 1.5rem;
    top: 1.8rem;
  }

  &__bottom {
    width: 14.8rem;

    @media (min-width: 1024px) {
      width: 100%;
      margin-top: 1rem;
    }

    span {
      @include InterMedium();
      font-size: 1.4rem;
      line-height: 130.02%;
      color: var(--black_1);

      @media (min-width: 375px) {
        font-size: 1.6rem;
      }
    }
  }
}